@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap");

:root {
	--font-family: "Manrope", sans-serif;

	--gradient-text: linear-gradient(89.97deg, #ae67fa 1.84%, #f74ea5 102.67%);
	--gradient-bar: linear-gradient(103.22deg, #ae67fa -13.86%, #f74ea5 99.55%);

	--color-bg: #031b34;
	--color-footer: #131725;
	--color-blog: #042c54;
	--color-text: #d6f6f6;
	--color-subtext: #fe9ccf;
}

a {
	color: unset;
	text-decoration: none;
}

/**
 * ----------------------------------------
 * Gradient bg color
  * Generated by angrytools.com/gradient/
 * ----------------------------------------
 */

.gradient__bg {
	/* ff 3.6+ */
	background: -moz-radial-gradient(
		circle at 3% 25%,
		rgba(0, 40, 83, 1) 0%,
		rgba(4, 12, 24, 1) 100%
	);

	/* safari 5.1+,chrome 10+ */
	background: -webkit-radial-gradient(
		circle at 3% 25%,
		rgba(0, 40, 83, 1) 0%,
		rgba(4, 12, 24, 1) 100%
	);

	/* opera 11.10+ */
	background: -o-radial-gradient(
		circle at 3% 25%,
		rgba(0, 40, 83, 1) 0%,
		rgba(4, 12, 24, 1) 100%
	);

	/* ie 10+ */
	background: -ms-radial-gradient(
		circle at 3% 25%,
		rgba(0, 40, 83, 1) 0%,
		rgba(4, 12, 24, 1) 100%
	);

	/* global 92%+ browsers support */
	background: radial-gradient(
		circle at 3% 25%,
		rgba(0, 40, 83, 1) 0%,
		rgba(4, 12, 24, 1) 100%
	);
}

.gradient__text {
	background: var(--gradient-text);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.section__padding {
	padding: 4rem 6rem;
}

.section__margin {
	margin: 4rem 6rem;
}

/**
 * ----------------------------------------
 * animation scale-up-center
  * Generated by Animista w: http://animista.net
 * ----------------------------------------
 */

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1)
		both;
	animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

@-webkit-keyframes scale-up-center {
	0% {
		-webkit-transform: scale(0.5);
		transform: scale(0.5);
	}
	100% {
		-webkit-transform: scale(1);
		transform: scale(1);
	}
}
@keyframes scale-up-center {
	0% {
		-webkit-transform: scale(0.5);
		transform: scale(0.5);
	}
	100% {
		-webkit-transform: scale(1);
		transform: scale(1);
	}
}

/*  Media style small/medium devices */
@media screen and (max-width: 700px) {
	.section__padding {
		padding: 4rem;
	}

	.section__margin {
		margin: 4rem;
	}
}

@media screen and (max-width: 550px) {
	.section__padding {
		padding: 4rem 2rem;
	}

	.section__margin {
		margin: 4rem 2rem;
	}
}
